import React from "react";
import { useState, useEffect } from "react";
import { FaBars } from "react-icons/fa";
import { IconContext } from "react-icons/lib";
import { animateScroll as scroll } from "react-scroll";

import {
	Nav,
	NavbarContainer,
	NavLogo,
	MobileIcon,
	NavMenu,
	NavItem,
	NavLinks,
	NavBtn,
	NavBtnLink,
} from "./NavbarElements";

const Navbar = ({ toggle }) => {
	const [scrollNav, setScrollNav] = useState(false);

	const changeNav = () => {
		if (window.scrollY >= 80) {
			setScrollNav(true);
		} else {
			setScrollNav(false);
		}
	};

	useEffect(() => {
		window.addEventListener("scroll", changeNav);
	}, []);

	const toggleHome = () => {
		scroll.scrollToTop();
	};

	return (
		<>
			<IconContext.Provider value={{ color: "#fff" }}>
				<Nav scrollNav={scrollNav}>
					<NavbarContainer>
						<NavLogo to="/" onClick={toggleHome}>
							Brandon Ferrari
						</NavLogo>
						<MobileIcon>
							<FaBars onClick={toggle} />
						</MobileIcon>
						<NavMenu>
							<NavItem>
								<NavLinks
									to="home"
									smooth={true}
									duration={500}
									spy={true}
									exact="true">
									Home
								</NavLinks>
							</NavItem>
							<NavItem>
								<NavLinks
									to="about"
									smooth={true}
									duration={500}
									spy={true}
									exact="true">
									About
								</NavLinks>
							</NavItem>
							<NavItem>
								<NavLinks
									to="experience"
									smooth={true}
									duration={500}
									spy={true}
									exact="true">
									Experience
								</NavLinks>
							</NavItem>
							<NavItem>
								<NavLinks
									to="projects"
									smooth={true}
									duration={500}
									spy={true}
									exact="true">
									Projects
								</NavLinks>
							</NavItem>
						</NavMenu>
						<NavBtn>
							<NavBtnLink to="resume">Resume</NavBtnLink>
						</NavBtn>
					</NavbarContainer>
				</Nav>
			</IconContext.Provider>
		</>
	);
};

export default Navbar;
