import React from "react";
import Icon1 from "../../images/flag-france.jpg";
import Icon2 from "../../images/uwindsor.jpg";
import Icon3 from "../../images/wfh.jpg";

import {
	AboutContainer,
	AboutH1,
	AboutWrapper,
	AboutCard,
	AboutIcon,
	AboutH2,
	AboutP,
} from "./AboutElements";

const About = () => {
	return (
		<AboutContainer id="about">
			<AboutH1>About Me</AboutH1>
			<AboutWrapper>
				<AboutCard>
					<AboutIcon src={Icon1} />
					<AboutH2>Bilingual</AboutH2>
					<AboutP>
						Did my studies in French for 14+ years and would consider myself
						fluent in the language
					</AboutP>
				</AboutCard>
				<AboutCard>
					<AboutIcon src={Icon2} />
					<AboutH2>University of Windsor Graduate</AboutH2>
					<AboutP>
						Completed a 5 year Computer Science program and remained on the
						Dean's Honour roll through its entirety
					</AboutP>
				</AboutCard>
				<AboutCard>
					<AboutIcon src={Icon3} />
					<AboutH2>2+ Years of Work Experience</AboutH2>
					<AboutP>
						I've worked at various companies ranging from startups to global
						corporations and have good experience in both lone wolf and team
						oriented environments
					</AboutP>
				</AboutCard>
			</AboutWrapper>
		</AboutContainer>
	);
};

export default About;
