import React from "react";

import { ProjectsContainer, Title } from "./ProjectsElements";
import InfoSection from "../InfoSection";
import { ProjectWebsite, ProjectWECCC, ProjectApp } from "../InfoSection/Data";

const Projects = () => {
	return (
		<ProjectsContainer id="projects">
			<Title>Projects</Title>
			<InfoSection {...ProjectWebsite} />
			<InfoSection {...ProjectWECCC} />
			<InfoSection {...ProjectApp} />
		</ProjectsContainer>
	);
};

export default Projects;
