import React from "react";
import { FaLinkedin, FaGithub } from "react-icons/fa";

import {
	FooterContainer,
	FooterWrapper,
	SocialMedia,
	SocialMediaWrap,
	SocialLogo,
	SocialIcons,
	SocialIconLink,
} from "./FooterElements";

const Footer = () => {
	return (
		<FooterContainer>
			<FooterWrapper>
				<SocialMedia>
					<SocialMediaWrap>
						<SocialLogo to="home">Brandon Ferrari</SocialLogo>
						<SocialIcons>
							<SocialIconLink
								href="https://www.linkedin.com/in/brandonferrari/"
								target="_blank"
								aria-label="LinkedIn">
								<FaLinkedin />
							</SocialIconLink>
							<SocialIconLink
								href="https://github.com/ferrarib"
								target="_blank"
								aria-label="GitHub">
								<FaGithub />
							</SocialIconLink>
						</SocialIcons>
					</SocialMediaWrap>
				</SocialMedia>
			</FooterWrapper>
		</FooterContainer>
	);
};

export default Footer;
