import React from "react";

import { ExperienceContainer, Title } from "./ExperienceElements";
import InfoSection from "../InfoSection";
import {
	ExperienceSymatron,
	ExperienceGroundEffects,
	ExperienceHiramWalker,
	ExperienceTA,
} from "../InfoSection/Data";

const Experience = () => {
	return (
		<ExperienceContainer id="experience">
			<Title>Experience</Title>
			<InfoSection {...ExperienceHiramWalker} />
			<InfoSection {...ExperienceGroundEffects} />
			<InfoSection {...ExperienceSymatron} />
			<InfoSection {...ExperienceTA} />
		</ExperienceContainer>
	);
};

export default Experience;
